.container {
    background: rgb(57, 56, 56);
    width: 100%;
    padding: 1rem;
    border: 2px solid white;
    border-radius: 4px;
}

.error {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    color: rgb(231, 135, 135);
    margin-left: 3rem;
    margin-bottom: 1rem;
    
}
.loginSection {
    display: flex;
    flex-direction: row;
    margin-left: 1rem;
}
.label {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: white;
    font-size: 18px; 
    text-align: center;
    padding-top: 0.5rem;
    margin-right: 2rem;
    margin-left: 2rem;
    height: 2rem;
}
.input {
    height: 1.5rem;
}
.btn {
    font-size: 14px;
    height: 2.4rem;
    width: 6rem;
    color: white;
    background: grey;
    border-radius: 3px;
    margin-left: 2rem;
    border: 2px solid white
}
.btn:hover {
    cursor: pointer;
    background-color: bisque;
    color: black;
}